// data-set 可以按需引入，除此之外不要引入别的包
import React from 'react';
import { Chart, View, Geom, Label } from 'bizcharts';
import DataSet from '@antv/data-set';
import Data from './china.json';
import detailData from './detail.json';

let data = Data;
let userData = [];
function into(data){
  userData=[];
  let ds = new DataSet();
  let geoView = ds.createView();
  let geoDv = geoView.source(data, {
    type: 'GeoJSON',
  }).transform({
    type: 'map',
    callback: function callback(row) {
      userData.push({
        longitude: row.centroidX,
        latitude: row.centroidY,
        name: row.name,
        value: Math.random() * (1000 - 1),
      });
      return row;
    },
  });
  return geoDv;
}

let geoDv = into(data);
console.log('geoDv',geoDv);

const scale = {
  latitude: {
    sync: true,
    nice: false,
  },
  longitude: {
    sync: true,
    nice: false,
  },
};

class Demo extends React.Component {

  state={
    map:geoDv,
    user:userData,
  }
  
  componentDidMount(){
    setTimeout(() => {
      geoDv = into(detailData);
      this.setState({map:geoDv})
      console.log('!!!',geoDv);
    }, 4000);
  }
  
  render() {
    
    return (
      <Chart height={window.innerHeight/1.8} onPlotClick={ev=>{console.log(ev.data)}} scale={scale}  forceFit padding={0}>
        {/* // geo view */}
        <View data={this.state.map}>
          <Geom type="polygon" active={true} color= {["name", ["#7f8da9", "#fec514", "#daf0fd"]]} position="longitude*latitude" >
            <Label content="name" offset={1} />
          </Geom>
        </View>
        {/* // user view */}
        <View data={userData}>
          <Geom
            type="point"
            position="longitude*latitude"
            color={['value', 'red']}
            size={5}
            style={{
              blur: 1,
            }}
          />
        </View>
      </Chart>
    );
  }
}

export default Demo;
