import React from "react";
import {
  Chart,
  Geom,
  Axis,
  Tooltip,
  Legend,
} from "bizcharts";
import DataSet from "@antv/data-set";
console.log(Geom);
class FailureTimeDistribution extends React.Component {
  render() {
    const data = [
      {
        month: "一月",
        奇瑞: 7.0,
        捷达: 3.9
      },
      {
        month: "二月",
        奇瑞: 6.9,
        捷达: 4.2
      },
      {
        month: "三月",
        奇瑞: 9.5,
        捷达: 5.7
      },
      {
        month: "四月",
        奇瑞: 14.5,
        捷达: 8.5
      },
      {
        month: "五月",
        奇瑞: 18.4,
        捷达: 11.9
      },
      {
        month: "六月",
        奇瑞: 21.5,
        捷达: 15.2
      },
      {
        month: "七月",
        奇瑞: 25.2,
        捷达: 17.0
      },
      {
        month: "八月",
        奇瑞: 26.5,
        捷达: 16.6
      },
      {
        month: "九月",
        奇瑞: 23.3,
        捷达: 14.2
      },
      {
        month: "十月",
        奇瑞: 55.3,
        捷达: 10.3
      },
      {
        month: "十一月",
        奇瑞: 13.9,
        捷达: 6.6
      },
      {
        month: "十二月",
        奇瑞: 9.6,
        捷达: 4.8
      }
    ];
    const ds = new DataSet();
    const dv = ds.createView().source(data);
    dv.transform({
      type: "fold",
      fields: ["奇瑞", "捷达"],
      // 展开字段集
      key: "city",
      // key字段
      value: "temperature" // value字段
    });
    console.log(dv);
    const cols = {
      month: {
        range: [0, 1]
      }
    };
    if (this.chart) {
        this.chart.forceFit();
      }
    return (
      <div>
        <Chart height={window.innerHeight/4.1} data={dv} scale={cols} forceFit onGetG2Instance={(chart) => {
        this.chart = chart;
      }}>
          <Legend />
          <Axis name="month" />
          <Axis
            name="temperature"
            label={{
              formatter: val => `${val}`
            }}
          />
          <Tooltip
            crosshairs={{
              type: "y"
            }}
          />
          <Geom
            type="line"
            position="month*temperature"
            size={2}
            color={"city"}
            shape={"smooth"}
          />
          <Geom
            type="point"
            position="month*temperature"
            size={4}
            shape={"circle"}
            color={"city"}
            style={{
              stroke: "#fff",
              lineWidth: 1
            }}
          />
        </Chart>
      </div>
    );
  }
}

export default FailureTimeDistribution;
