import { DatePicker, Select } from 'antd'
import React from 'react';
import {connect} from 'react-redux';
import CarPick from '../actions/CarPick-MonthPick/CarPick.js';


class Picktime extends React.Component{
  render(){
    const { Option, OptGroup } = Select;
    const { MonthPicker } = DatePicker;
    let {dispatch} = this.props;
    return(
    <div>
        <Select
          onChange={function(){
            dispatch(CarPick('白龙马'));
          }}
          defaultValue="全部车型"
          style={{ width: 200 }}
        >
          <OptGroup label="单独选择">
            <Option value="福特">福特</Option>
            <Option value="本田">本田</Option>
            <Option value="奔驰">奔驰</Option>
            <Option value="宝马">宝马</Option>
            <Option value="马自达">马自达</Option>
          </OptGroup>
          <OptGroup label="全部选择">
            <Option value="全部车型">全部车型</Option>
          </OptGroup>
        </Select>  
        <MonthPicker placeholder="Select month" />
    </div>
    )
  }
}

let picktime = connect()(Picktime)

export default picktime  