import React from "react";
import {
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
} from "bizcharts";
import DataSet from "@antv/data-set";

class failureCarDistribution extends React.Component {
  render() {
    const { DataView } = DataSet;
    const data = [
      {
        item: "瑞虎",
        count: 40
      },
      {
        item: "QQ",
        count: 21
      },
      {
        item: "奇瑞",
        count: 17
      },
      {
        item: "捷达",
        count: 13
      },
      {
        item: "其他",
        count: 9
      }
    ];
    const dv = new DataView();
    dv.source(data).transform({
      type: "percent",
      field: "count",
      dimension: "item",
      as: "percent"
    });
    const cols = {
      percent: {
        formatter: val => {
          val = val * 100 + "%";
          return val;
        }
      }
    };
    return (
      <div>
        <Chart
          height={window.innerHeight/6.5}
          data={dv}
          scale={cols}
          forceFit
          padding={[0,0,30,0]}
        >
          <Coord type="theta" radius={1} />
          
        
          <Tooltip
            showTitle={false}
            itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
          />
          <Geom
            type="intervalStack"
            active={true}
            position="percent"
            color="item"
            tooltip={[
              "item*percent",
              (item, percent) => {
                percent = percent * 100 + "%";
                return {
                  name: item,
                  value: percent
                };
              }
            ]}
            style={{
              lineWidth: 1,
              stroke: "#fff"
            }}
          >
            <Label
              content="percent"
              formatter={(val, item) => {
                return item.point.item + ": " + val;
              }}
            />
          </Geom>
        </Chart>
      </div>
    );
  }
}

export default failureCarDistribution;
