import axios from 'axios';


function receivePosts(carName,data){
    return{
        type:'UpdateTime',
        carName:carName,
        TimeChange:data
    }
}

export default function fetchPostCars(carName){
    let data = [{
        year: '1996',
        value: 6,
      },
      {
        year: '1997',
        value: 7,
      },
      {
        year: '1998',
        value: 9,
      },];
    return function(dispatch){

        // return axios('http://www.baidu.com')
        // .then(res=>{res.json()})
        // .then(data=>dispatch(receivePosts(carName, data)))
        return dispatch(receivePosts(carName, data));
    }
}