import React, { Component } from 'react';
import { Row, Col } from 'antd';
import 'antd/dist/antd.css';
import Totalchart from '../../Components/bizchars/totalAnalyze.jsx';
import Picktime from '../../Components/picktime.jsx';
import ListRender from '../../Components/listRender.jsx';
import PopView from '../Popview/index.jsx';
import FailureTimeDistribution from '../../Components/failureTimeDistribution.jsx';
import FailureCarDistribution from '../../Components/failureCarDistribution.jsx';
import UserCarDistribution from '../../Components/userCarDistribution.jsx';
import FailureHistogram from '../../Components/failureHistogram.jsx';
import Drawit from '../../Components/china-map/drawit.jsx';

import './index.css';

class Home extends Component {
    state = {
      breakdownNumber: 123456,
      breakdownClassNumber: 11,
      breakdownClass: ['驱动系统故障报警', '制动系统故障报警', '电池管理系统故障报警'],
    }
  
    render() {
      return (
        <Row style={{ height: '100%', width: '100%' }}>
          <Col className="changeHeight" span={8}>
            <div>
              <div className='totalfailure-title'>用户时间变化</div>
              <div style={{ display: 'inline-block', width: '48%' }}>
                <Totalchart />
                <Totalchart />
              </div>
              <div style={{ display: 'inline-block', width: '48%' }}>
                <Totalchart />
                <Totalchart />
              </div>
            </div>
            <div>
              <div className='single-title'>用户地区车型分布</div>
              {/* <PopView/> */}
              <div className='failureDistribution'>
                <UserCarDistribution/>
              </div>
              <div className='failureDistribution'>
                <UserCarDistribution/>
              </div>
            </div>
          </Col>
          <Col className="changeHeight" span={8}>
            <div className='title'>车联网大数据应用平台</div>
            <Picktime />
            {/* 这里地图加一个回退按钮，以防误点之后，无法返回到全国地图 */}
            <div>
              <Drawit />
            </div>
            <div>
              <p>故障数量</p>
              <p>{this.state.breakdownNumber}</p>
              <p>涉及故障类别</p>
              <p>{this.state.breakdownClassNumber}个</p>
              <p>涉及故障类别</p>
              <ListRender values={this.state.breakdownClass} />
            </div>
          </Col>
          <Col className="changeHeight" span={8}>
            <div>
              <div className='totalfailure-title'>综合故障</div>
              <div className='failureDistribution'>
                <div className='failure-title'>故障数时间分布图</div>
                <FailureTimeDistribution/>
              </div>
              <div className='failureDistribution'>
                <div className='failure-title'>故障单体分布图</div>
                <FailureHistogram/>
              </div>
              <div className='failure-title'>故障车型分布</div>
              <FailureCarDistribution/>
            </div>
            <div className='single-title'>单体故障</div>
            <div>
              <div className='failureDistribution'>
                
                <FailureTimeDistribution/>
              </div>
              <div className='failureDistribution'>
              <FailureCarDistribution/>
              </div>
            </div>
            <div className='stop-title'>制动系统故障分布</div>
            <ul className='stop-list'>
              <li>EPB</li>
              <li>EBD故障指示</li>
              <li>ABS故障指示</li>
            </ul>
          </Col>
        </Row>
      );
    }
}
export default Home;
