import React from 'react';
import {
  Chart,
  Geom,
  Axis,
  Tooltip,
  Legend,
} from 'bizcharts';
import {connect} from 'react-redux';
class TotalChart extends React.Component {
  
  state={
    height:230,
    Data:this.props.userTimeChange,
  }

  componentWillMount(){
    let height = window.screen.availHeight
    this.setState({height:height})
  }

  componentDidMount(){
    
  }
  
  render() {
    const cols = {
      value: {
        min: 0,
      },
      year: {
        range: [0, 1],
      },
    };
    if (this.chart) {
      this.chart.forceFit();
    }
    // 这里考虑获取屏幕整体高度，通过换算确定一个图形的具体高度，用px表示，通过变量传入，实现响应式
    return (
      <div >
        <Chart height={this.state.height/4.4}  data={this.props.userTimeChange} scale={cols} onGetG2Instance={g2Chart => {
    g2Chart.animate(false);
    console.log(g2Chart);
      }} forceFit>
          <Axis name="year" />
          <Axis name="value" />
          <Tooltip
            crosshairs={{
              type: 'y',
            }}
          />
          <Geom type="line" position="year*value" size={2} />
          <Geom
            type="point"
            position="year*value"
            size={4}
            shape={'circle'}
            style={{
              stroke: '#fff',
              lineWidth: 1,
            }}
          />
        </Chart>
      </div>
    );
  }
}

function mapStateToProps(state){
  let userTimeChange = state.userTime;
  console.log('state',userTimeChange);
  return {
    userTimeChange
  }
}


export default connect(mapStateToProps)(TotalChart);
